import React, { useState } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

// CSS
import './css/App.css';
import './css/index.css';

// Layout / Common
import Navbar from './components/layout/Navbar';
import Footer from './components/layout/Footer';
import Cursor from './components/common/Cursor';

// Pages
import Top from './pages/Top';
import About from './pages/About';
import Book from './pages/Book';
import Travel from './pages/Travel';

const App: React.FC = () => {
  //  App.js  Contact 
  // About 
  const [showContact, setShowContact] = useState(true);

  const handleAboutScrollEnd = (atEnd: boolean) => {
    setShowContact(atEnd);
  };

  return (
    <Router>
      <div className="bg-white min-h-screen flex flex-col">
        {/*  */}
        <Cursor />
        
        {/*  */}
        <Navbar />
        
        {/*  */}
        <main className="flex-grow">
          <Switch>
            {/* About  props 
                render 
            */}
            <Route exact path="/watashi" component={Top} />
            
            <Route 
              path="/watashi/about" 
              render={() => <About onScrollEnd={handleAboutScrollEnd} />} 
            />
            
            <Route path="/watashi/book" component={Book} />
            
            <Route path="/watashi/travel" component={Travel} />
            
            {/* 404 */}
            <Route render={() => <Top />} />
          </Switch>
        </main>

        {/* 
            About showContact
           Footer  {showContact && <Footer />} 
        */}
        <Footer />
      </div>
    </Router>
  );
}

export default App;